<template>
    <div>
        <div>
            <el-table :data="emps" border stripe size="mini" style="width:95%" >
                <el-table-column type="selection" align="left" width="55"></el-table-column>
                <el-table-column prop="name" label="姓名" width="150"></el-table-column>
                <el-table-column prop="workID" label="工号" width="150"></el-table-column>
                <el-table-column prop="email" label="电子邮件" width="200"></el-table-column>
                <el-table-column prop="phone" label="电话号码" width="180"></el-table-column>
                <el-table-column prop="department.name" label="所属部门" width="120"></el-table-column>
                <el-table-column label="工资套账" align="center">
                    <template slot-scope="scope">
                        <el-tooltip placement="right" v-if="scope.row.salary">
                            <div slot="content">
                                <table>
                                    <tr>
                                        <td>基本工资</td>
                                        <td>{{scope.row.salary.basicsalary}}</td>
                                    </tr>
                                    <tr>
                                        <td>交通补助</td>
                                        <td>{{scope.row.salary.trafficsalary}}</td>
                                    </tr>
                                    <tr>
                                        <td>午餐补助</td>
                                        <td>{{scope.row.salary.lunchsalary}}</td>
                                    </tr>
                                    <tr>
                                        <td>奖金</td>
                                        <td>{{scope.row.salary.bonus}}</td>
                                    </tr>
                                    <tr>
                                        <td>养老金比率</td>
                                        <td>{{scope.row.salary.pensionper}}</td>
                                    </tr>
                                    <tr>
                                        <td>养老金基数</td>
                                        <td>{{scope.row.salary.pensionbase}}</td>
                                    </tr>

                                    <tr>
                                        <td>公积金比率</td>
                                        <td>{{scope.row.salary.accumulationfundper}}</td>
                                    </tr>
                                    <tr>
                                        <td>公积金基数</td>
                                        <td>{{scope.row.salary.accumulationfundbase}}</td>
                                    </tr>
                                    <tr>
                                        <td>医疗保险比率</td>
                                        <td>{{scope.row.salary.medicalper}}</td>
                                    </tr>
                                    <tr>
                                        <td>医疗保险基数</td>
                                        <td>{{scope.row.salary.medicalbase}}</td>
                                    </tr>
                                    <tr>
                                        <td>启用时间</td>
                                        <td>{{scope.row.salary.createdate}}</td>
                                    </tr>
                                </table>
                            </div>
                            <el-tag>{{scope.row.salary.name}}</el-tag>
                        </el-tooltip>
                        <el-tag v-else>暂未设置</el-tag>
                    </template>
                </el-table-column>
                <el-table-column label="操作" align="center" width="200px">
                    <template slot-scope="scope">
                        <el-popover
                                placement="left"
                                title="工资账套"
                                width="200"
                                trigger="click"
                                @show="showPop(scope.row.salary)"
                                @hide="hidePop(scope.row)"
                                >
                            <el-select v-model="selectSalary" placeholder="请选择" size="mini">
                                <el-option
                                        v-for="item in salaries"
                                        :key="item.id"
                                        :label="item.name"
                                        :value="item.id">
                                </el-option>
                            </el-select>
                            <el-button slot="reference" size="small" type="danger">修改工资套账</el-button>
                        </el-popover>
                    </template>
                </el-table-column>
            </el-table>
            <div style="display: flex;justify-content: left">
                <el-pagination
                        background
                        @current-change="currentChange"
                        @size-change="sizeChange"
                        layout="sizes, prev, pager, next, jumper, ->, total, slot"
                        :total="total">
                </el-pagination>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "SalSobCfg",
        data(){
            return{
                //员工套账
                emps:[],
                //套账
                salaries:[],
                //默认选中的员工套账
                selectSalary:null,
                //当前页
                page:1,
                //每页的大小
                size:10,
                //总条数
                total:10,
            }
        },

        mounted () {
            this.initSalSobCfg();
            this.initSalaries();
        },

        methods:{
            /**
             * 分页
             * 页数大小改变时
             * */
            sizeChange(size){
                this.size = size;
                this.initSalSobCfg();
            },
            /**
             * 分页
             * 页数改变时
             * */
            currentChange(page){
                this.page = page;
                this.initSalSobCfg();
            },
            /**
             * 加载套账
             * */
            initSalaries(){
                this.getRequest("/salary/sobcfg/salaries").then(resp=>{
                   if (resp) {
                       this.salaries = resp;
                   }
                });
            },

            /**
             * 加载员工套账
             * */
            initSalSobCfg(){
                this.getRequest("/salary/sobcfg/?page="+this.page+'&size='+this.size).then(resp=>{
                   if (resp) {
                       this.emps = resp.data;
                       this.total = resp.total;
                   }
                });
            },

            /**
             * 展示弹出框
             * */
            showPop(data){
                if (data){
                    this.selectSalary = data.id;
                } else {
                    this.selectSalary = null;
                }
            },

            /**
             * 关闭弹出框，修改原账套
             * */
            hidePop(data){
                if (this.selectSalary) {
                    let eid = data.id;
                    let sid = this.selectSalary;
                    let url = "/salary/sobcfg/?" + 'eid=' + eid + "&sid=" + sid;
                    this.putRequest(url).then(resp => {
                        if (resp) {
                            this.initSalSobCfg();
                        }
                    });
                }
            }
        },
    }
</script>

