<template>
    <div>
        <div style="display: flex;justify-content: left">
            <el-button icon="el-icon-plus" type="primary" @click="addSalary">添加工资账套</el-button>
            <el-button icon="el-icon-refresh" type="success" @click="initSalaries">刷新</el-button>
        </div>
        <div style="margin-top: 10px">
            <el-table
                    :data="salares"
                    style="width: 100%"
                    border
                    stripe>
                <el-table-column type="selection" width="55"></el-table-column>
                <el-table-column  width="150" prop="name" label="账套名称"></el-table-column>
                <el-table-column  width="100" prop="basicsalary" label="基本工资"></el-table-column>
                <el-table-column  width="100" prop="trafficsalary" label="交通补助"></el-table-column>
                <el-table-column  width="100" prop="lunchsalary" label="午餐补助"></el-table-column>
                <el-table-column  width="100" prop="bonus" label="奖金"></el-table-column>
                <el-table-column  width="130" prop="createdate" label="启用时间"></el-table-column>

                <el-table-column label="养老金" align="center">
                    <el-table-column  width="120" prop="pensionper" label="比率"></el-table-column>
                    <el-table-column  width="120" prop="pensionbase" label="基数"></el-table-column>
                </el-table-column>

                <el-table-column label="医疗保险" align="center">
                    <el-table-column  width="120" prop="medicalper" label="比率"></el-table-column>
                    <el-table-column  width="120" prop="medicalbase" label="基数"></el-table-column>
                </el-table-column>

                <el-table-column label="公积金" align="center">
                    <el-table-column  width="120" prop="accumulationfundper" label="比率"></el-table-column>
                    <el-table-column  width="120" prop="accumulationfundbase" label="基数"></el-table-column>
                </el-table-column>

                <el-table-column
                        label="操作"
                        align="center"
                        width="200px">
                    <template slot-scope="scope">
                        <el-button size="small" @click="editSalary(scope.row)">编辑</el-button>
                        <el-button size="small" type="danger" @click="deleteSalary(scope.row)">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>
        <el-dialog
                :title="title"
                :visible.sync="dialogVisible"
                width="50%">
             <!--进度条-->
             <div style="display: flex;justify-content: space-around;align-items: center">
                 <el-steps direction="vertical" :active="activeItemIndex">
                     <el-step :title="itemName"  v-for="(itemName,index) in salaryItemName" :key="index"></el-step>
                 </el-steps>
                 <el-input v-model="salary[title]" :placeholder="'请输入'+salaryItemName[index]+'....'" v-for="(value,title,index) in salary"
                           :key="index"  v-show="activeItemIndex == index " style="width: 250px;"></el-input>
             </div>
            <span slot="footer" class="dialog-footer">
                <el-button @click="preStep">{{activeItemIndex==10?'取消':'上一步'}}</el-button>
                <el-button type="primary" @click="nextStep">{{activeItemIndex==10?'完成':'下一步'}}</el-button>
          </span>
        </el-dialog>
    </div>

</template>

<script>
    export default {
        name: "SalSob",
        data(){
            return{
                title:null,
                //工资套账
                salares:[],
                //对话框
                dialogVisible:false,
                //默认最开始的进度条
                activeItemIndex:0,
                //工资条的名称
                salaryItemName:[
                    '基本工资',
                    '交通补助',
                    '午餐补助',
                    '奖金',
                    '养老金比率',
                    '养老金基数',
                    '医疗保险比率',
                    '医疗保险基数',
                    '公积金比率',
                    '公积金基数',
                    '账套名称',
                ],
                //添加的套账
                salary:{
                    basicsalary:0,
                    trafficsalary:0,
                    lunchsalary:0,
                    bonus:0,
                    pensionper:0,
                    pensionbase:0,
                    medicalper:0,
                    medicalbase:0,
                    accumulationfundper:0,
                    accumulationfundbase:0,
                    name:null,
                },
            }
        },
        mounted() {
            this.initSalaries();
        },
        methods:{

            /**
             * 添加工资套账
             * */
            addSalary(){
                this.title = "添加工资套账";
                this.dialogVisible = true;
                this.activeItemIndex = 0;
                //数据初始化
                 this.salary = {
                     basicsalary:null,
                     trafficsalary:null,
                     lunchsalary:null,
                     bonus:null,
                     pensionper:null,
                     pensionbase:null,
                     medicalper:null,
                     medicalbase:null,
                     accumulationfundper:null,
                     accumulationfundbase:null,
                     name:null,
                 };
            },

            /**
             * 查询所有工资套账
             * */
            initSalaries(){
                this.getRequest("/salary/sob/").then(resp=>{
                    this.salares = resp;
                })
            },

            /**
             * 下一步
             * */
            nextStep(){
                this.activeItemIndex++ ;
                if (this.activeItemIndex == 11){
                    if (this.salary.id){
                        this.putRequest("/salary/sob/",this.salary).then(resp=>{
                           if (resp){
                               this.dialogVisible = false;
                               this.initSalaries();
                               this.activeItemIndex = 0;
                           }
                        });
                        return;
                    } else {
                        delete this.salary.id;
                        this.postRequest("/salary/sob/", this.salary).then(resp => {
                            this.initSalaries();
                            this.dialogVisible = false;
                            this.activeItemIndex = 0;
                        });
                        return;
                    }
                }
            },

            /**
             * 上一步
             * */
            preStep(){
                if (this.activeItemIndex == 0){
                    return ;
                } else if (this.activeItemIndex == 10){
                    //关闭对话框
                    this.dialogVisible = false;
                    this.activeItemIndex = 0;
                    return ;
                }
                this.activeItemIndex--;

            },

            /**
             * 删除账单
             * */
            deleteSalary(data){
                this.$confirm('此操作将永久删除【'+data.name+'】, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.deleteRequest("/salary/sob/"+data.id).then(resp=>{
                        this.initSalaries();
                    });
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },

            /**
             * 编辑账单
             * */
            editSalary(data){
                this.title = "修改工资套账";
                this.dialogVisible = true;
                this.salary = {
                    basicsalary:data.basicsalary,
                    trafficsalary:data.trafficsalary,
                    lunchsalary:data.lunchsalary,
                    bonus:data.bonus,
                    pensionper:data.pensionper,
                    pensionbase:data.pensionbase,
                    medicalper:data.medicalper,
                    medicalbase:data.medicalbase,
                    accumulationfundper:data.accumulationfundper,
                    accumulationfundbase:data.accumulationfundbase,
                    name: data.name,
                    id:data.id,
                };

            },
        }
    }
</script>

